// 校验信息展示

import React, { PropTypes } from 'react';
import classnames from 'classnames';

import Icon from '../../icon/index';

export default class Msg extends React.Component {
  constructor(props) {
    super(props);

    this.iconMap = {
      info: 'info',
      success: 'success',
      error: 'alert'
    };
  }

  static PropTypes = {
    // 状态分为3种:
    // success: 显示成功状态及信息
    // info: 显示info状态及信息
    // error: 显示错误状态及信息
    status: PropTypes.oneOf(['info', 'success', 'error']),
    // 以数组形式传递，方便扩展
    msgs: PropTypes.array
  };

  static defaultProps = {
    prefixCls: 'im-msg',
    status: null,
    msgs: []
  };

  render() {
    const { prefixCls, status, msgs } = this.props;
    const msg = msgs.length ? msgs[0] : '';

    const allCls = {
      [prefixCls]: true,
      [`${prefixCls}--${status}`]: true
    };

    return (
      <div className={classnames(allCls)}>
        <Icon
          className={`${prefixCls}-icon`}
          type={this.iconMap[status]}
        />
        <p className={`${prefixCls}-text`}>
          {msg}
        </p>
      </div>
    );
  }
}
